.s-speakers {
  padding: clamp(50px, 5vw, 100px) 0;

  .container {
    max-width: 1440px; } }

.speakers-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px 20px;

  @include xxs {
    gap: 20px 10px; }

  &__item {
    max-width: 15.4%;
    min-width: 200px;

    @include sm {
      min-width: 160px; }

    @include xxs {
      min-width: 120px;
      max-width: calc(50% - 5px); }

    &_cfp {
      padding: 64px 15px;
      border: 5px solid $gray2; } }

  .btn {
    border: 3px solid $gray2;
    background: $mainBg;
    margin: 16px 0 0;
    color: $themeColorD;
    padding: 10px 20px;
    display: inline-flex;
    min-width: 170px;

    &:after,
    &:before {
      display: none; }

    @include sm {
      min-width: 120px; }

    &:hover {
      background: transparent;
      border-color: $themeColorD; } }

  a {
    color: $themeColor; } }

.speaker {
  color: $white;

  a {
    color: $themeColor; }

  &__tag {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 10px;
    top: 10px;
    background: $black;
    height: 26px;
    padding: 0 10px;
    border-radius: 10px;
    font-size: 12px; }

  &__photo {
    position: relative;
    padding-bottom: 100%;
    margin: 0 0 18px;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 20px;
      transition: all ease 0.1s; }

    .activity-holder {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      // opacity: 0
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
      font-weight: 900;
      font-size: 16px;

      cursor: pointer;
      .slider {
        opacity: 0;
        transition: all ease 0.1s; }
      &:focus-visible, &:hover {
        .slider {
          opacity: 1; }
        & ~ picture img {

          filter: brightness(0.4); } } } }



  &__head {

    &+p {
      margin: 40px 0 18px; } }

  &__name {
    font-size: clamp(18px, 2vw, 22px);
    font-weight: 900;
    line-height: 0.92;
    text-transform: uppercase; }

  &__company {
    line-height: 1.52;
    font-size: 12px;
    margin: 8px 0; }

  &__talk {
    line-height: 1.52;
    font-size: 12px;
    margin: 12px 0;

    span {
      color: $gray3; } }

  &__desc {
    line-height: 1.52;
    font-size: 12px;
    margin: 12px 0;
    color: $gray3;

    p {
      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;

      &:not(:first-child) {
        display: none; } } }

  &__socials {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    gap: 5px;
    padding: 10px; }

  &__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: $mainBg;

    .icon {
      width: 18px;
      height: 18px;
      fill: $white;
      transition: all ease 0.1s; }

    &:hover, &:focus-visible {

      .icon {
        fill: $themeColor; } } } }
