.s-prices {
	color: #fff;
	padding: clamp(50px, 5vw, 100px) 0;

	.container {
		max-width: 1200px;

		@include tablet {
			padding: 0; }

		@include md {
			padding: 0; }

		@include sm {
			padding: 0; } } }

.prices {
	background: $mainBg;
	font-size: 16px;
	position: relative;

	&__countdown {
		position: absolute;
		top: 126px;
		font-size: 18px;
		left: 0;
		right: 0;
		text-align: center;
		line-height: 0.9;
		text-transform: uppercase;
		font-weight: 900;
		color: #00FF000D;
		letter-spacing: 0.05em;
		display: none;

		@include sm {
			position: relative;
			top: 0; }

		b {
			font-weight: 900;
			display: block;
			font-size: clamp(32px, 16vw, 170px);

			@include sm {
				font-size: clamp(32px, 14vw, 170px); } } }



	&__list {
		position: relative;
		padding: 0 0 0;
		margin: 0;
		list-style: none;
		// display: flex

		@include sm {
			padding: 30px 0 0; } }

	&__item {
		display: flex;
		flex-direction: column;
		// flex: 1 1 auto
		text-align: center;
		padding: 20px 0 20px;
		// min-width: 300px

		@include sm {
			padding: 20px 0 20px;
			width: 310px; } }

	&__title {
		font-size: clamp(26px, 4vw, 32px);
		text-transform: uppercase;
		line-height: 0.94;
		font-weight: 900;
		margin: 0 0 20px;
		display: flex;
		flex-direction: column-reverse;

		em {
			font-style: normal;
			color: #FE5D02; }

		p {
			&:first-child {
				min-height: 92px;

				@include sm {
					min-height: 74px; } }


			&:last-child {
				font-size: 16px;
				font-weight: 400;
				text-transform: none;
				margin: 0 0 24px; } }

		@include sm {
			margin: 0 0 30px; } }

	&__price {
		@include regular;
		color: $white;
		margin: 0;

		h4 {
			@include black;
			font-size: 60px;
			line-height: 1;
			margin: 12px 0 12px; }

		strong {
			@include black;
			font-size: 94px;
			color: $white;
			margin: 0 0 20px;
			line-height: 1;

			&:before {
				content: '€';
				display: inline-block;
				position: relative;
				font-size: 38px;
				margin: 0 5px 0 0;
				top: -38px;
				@include medium; } }

		&+.prices__content {
			margin-top: 70px; } }

	&__price-tip {
		position: relative;
		display: inline-flex;
		font-size: 14px;
		color: #FF0000;
		font-weight: 900;
		margin: 0 auto;
		padding: 8px 3px;

		a {
			color: $themeColor; }

		&:after {
			content: "";
			position: absolute;
			left: 0px;
			right: 0px;
			top: 16px;
			height: 2px;
			background: #FF0000; } }

	&__content {
		display: flex;
		flex-direction: column;
		background: #1F1F1F;
		border-radius: 15px;
		padding: 18px 30px 32px;
		margin: 36px 10px 0;
		height: 100%;

		@include md {
			margin: 36px 10px 0; } }


	&__desc {
		text-align: left;
		font-size: 12px;
		margin: 0 0 40px;
		line-height: 1.12;

		p {
			position: relative;

			&:not(:first-child) {
				margin: 15px 0 0;

				@include sm {
					margin: 14px 0 0; } } }

		@include sm {
			margin: 15px 0 40px; }

		a {
			color: $themeColor; } }

	&__btn {
		margin: auto auto 0;
		display: inline-flex; }

	.swiper-slide {
		height: auto;

		@include sm {
			width: 90%; } }

	.prices-slider-prev,
	.prices-slider-next {
		--spacing: -90px;
		position: absolute;
		left: var(--spacing);
		top: 50%;
		border: 3px solid $gray2;
		width: 54px;
		height: 54px;
		border-radius: 50%;
		z-index: 2;
		transition: all ease 0.2s;
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba(22, 25, 23, 0.8);

		@include laptop {
			top: 35%;
			--spacing: 20px; }

		&:hover {
			cursor: pointer;
			border-color: $themeColor2; }

		.icon {
			width: 10px;
			height: 17px;
			fill: $themeColor2; }

		&.swiper-button-disabled {
			opacity: 0;
			visibility: hidden; } }

	.prices-slider-next {
		left: auto;
		right: var(--spacing); } }

